@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;900&display=swap);
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
body {
  background-color: rgb(163, 165, 250);
  transition: ease-in-out 2s;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15rem;
}
.creator {
  color: white;
  text-align: center;
  font-size: 12px;
}
@media (min-width: 900px) {
  .creator {
    font-size: 1rem;
  }
}

.box {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 300px;
  min-height: 100px;
  padding: 1rem;
  border-radius: 5px;
  box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
  font-size: 10px;
  margin-top: -5rem;
}

@media (min-width: 350px) {
  .box {
    width: 350px;
    min-height: 100px;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
    font-size: 12px;
    margin-top: -4rem;
  }
}
@media (min-width: 400px) {
  .box {
    width: 400px;
    min-height: 100px;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
    font-size: 12px;
    margin-top: -2rem;
  }
}

@media (min-width: 600px) {
  .box {
    font-size: 16px;
    width: 600px;
    min-height: 100px;
    padding: 1rem;
    border-radius: 5px;
    margin-top: 4rem;
    box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
  }
}
@media (min-width: 900px) {
  .box {
    margin-top: 5rem;
    width: 700px;
    min-height: 150px;
    padding: 1rem;
    border-radius: 5px;
    margin-top: 4rem;
    box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
  }
  .btn {
    margin-top: 3rem;
  }
}

.btn {
  border: solid #fff;
  text-decoration: none;
  outline: none;
  border-radius: 6px;
  padding: 8px;
  color: #fff;
  margin-top: 1rem;
}

@media (min-width: 900px) {
  .btn {
    margin-top: 1rem;
    padding: 1rem;
  }
}
.btnbox {
  text-align: center;
}

